"use client";

import { useState } from "react";
import { Bold, Italic, List, Tag as TagIcon, Save } from "lucide-react";

export function Editor({ selectedNote }: { selectedNote: any }) {
    const [content, setContent] = useState(selectedNote?.content || "");

    if (!selectedNote) {
        return (
            <div className="flex h-full items-center justify-center text-muted-foreground">
                Select a page to start writing
            </div>
        );
    }

    return (
        <div className="flex h-full flex-col">
            <div className="border-b border-border p-4">
                <input
                    type="text"
                    className="w-full bg-transparent text-3xl font-bold focus:outline-none"
                    defaultValue={selectedNote.title}
                    placeholder="Page Title"
                />
                <div className="mt-2 flex items-center space-x-2 text-sm text-muted-foreground">
                    <span>{selectedNote.date}</span>
                    <span>|</span>
                    <div className="flex items-center space-x-1">
                        <TagIcon className="h-3 w-3" />
                        <span>{selectedNote.tags?.join(", ") || "No tags"}</span>
                    </div>
                </div>
            </div>

            {/* Toolbar */}
            <div className="flex items-center border-b border-border bg-muted/20 px-4 py-2 space-x-2">
                <button className="p-1 hover:bg-muted rounded"><Bold className="h-4 w-4" /></button>
                <button className="p-1 hover:bg-muted rounded"><Italic className="h-4 w-4" /></button>
                <div className="h-4 w-[1px] bg-border mx-2" />
                <button className="p-1 hover:bg-muted rounded"><List className="h-4 w-4" /></button>
                <div className="flex-1" />
                <button className="flex items-center space-x-1 px-3 py-1 bg-primary text-primary-foreground text-xs rounded hover:bg-primary/90">
                    <Save className="h-3 w-3" />
                    <span>Save</span>
                </button>
            </div>

            <textarea
                className="flex-1 resize-none bg-transparent p-6 focus:outline-none font-mono text-sm leading-relaxed"
                placeholder="Start typing..."
                value={content}
                onChange={(e) => setContent(e.target.value)}
            />
        </div>
    );
}
