"use client";

import { useState } from "react";
import { Folder, FileText, Plus } from "lucide-react";
import { cn } from "@/lib/utils";
import { Editor } from "@/components/notebook/Editor";

// Mock Data
const sections = [
    { id: "s1", name: "Trade Notes", count: 12 },
    { id: "s2", name: "Daily Journal", count: 45 },
    { id: "s3", name: "Strategy", count: 5 },
    { id: "s4", name: "Backtesting", count: 8 },
];

const pages: Record<string, any[]> = {
    "s1": [
        { id: "p1", title: "AAPL Long Setup", date: "Oct 12, 2023", preview: "Entered at support level...", tags: ["Setups", "Long"] },
        { id: "p2", title: "ES Short Scalp", date: "Oct 11, 2023", preview: "Missed entry by 2 ticks...", tags: ["Mistake", "FOMO"] },
    ],
    "s2": [
        { id: "p3", title: "Market Review - Oct 12", date: "Oct 12, 2023", preview: "Market was chopping...", tags: ["Daily"] },
    ]
};

export default function NotebookPage() {
    const [activeSection, setActiveSection] = useState("s1");
    const [activePageId, setActivePageId] = useState<string | null>("p1");

    const currentPages = pages[activeSection] || [];
    const activePage = currentPages.find(p => p.id === activePageId) || null;

    return (
        <div className="flex h-screen max-h-[calc(100vh-2rem)] overflow-hidden m-4 rounded-xl border border-border bg-card shadow-sm">

            {/* Sections Sidebar (Left) */}
            <div className="w-60 border-r border-border bg-muted/10 flex flex-col">
                <div className="p-4 border-b border-border font-semibold flex justify-between items-center">
                    <span>Notebooks</span>
                    <button className="hover:bg-muted p-1 rounded"><Plus className="h-4 w-4" /></button>
                </div>
                <div className="flex-1 overflow-y-auto p-2 space-y-1">
                    {sections.map(section => (
                        <button
                            key={section.id}
                            onClick={() => { setActiveSection(section.id); setActivePageId(null); }}
                            className={cn(
                                "w-full flex items-center space-x-2 px-3 py-2 rounded-md text-sm font-medium hover:bg-muted transition-colors",
                                activeSection === section.id ? "bg-primary/10 text-primary" : "text-muted-foreground"
                            )}
                        >
                            <Folder className="h-4 w-4" />
                            <span className="flex-1 text-left">{section.name}</span>
                            <span className="text-xs opacity-50">{section.count}</span>
                        </button>
                    ))}
                </div>
            </div>

            {/* Pages Sidebar (Middle) */}
            <div className="w-72 border-r border-border bg-background flex flex-col">
                <div className="p-4 border-b border-border font-semibold flex justify-between items-center">
                    <span>Pages</span>
                    <button className="hover:bg-muted p-1 rounded"><Plus className="h-4 w-4" /></button>
                </div>
                <div className="flex-1 overflow-y-auto">
                    {currentPages.length === 0 ? (
                        <div className="p-4 text-sm text-muted-foreground text-center">No pages here</div>
                    ) : (
                        currentPages.map(page => (
                            <button
                                key={page.id}
                                onClick={() => setActivePageId(page.id)}
                                className={cn(
                                    "w-full flex-col items-start px-4 py-3 border-b border-border/50 hover:bg-muted/50 transition-colors text-left",
                                    activePageId === page.id ? "bg-muted/50 border-l-2 border-l-primary pl-[14px]" : ""
                                )}
                            >
                                <h4 className={cn("text-sm font-semibold", activePageId === page.id ? "text-primary" : "")}>{page.title}</h4>
                                <div className="flex items-center justify-between mt-1">
                                    <span className="text-xs text-muted-foreground">{page.date}</span>
                                </div>
                                <p className="text-xs text-muted-foreground mt-1 line-clamp-1">{page.preview}</p>
                            </button>
                        ))
                    )}
                </div>
            </div>

            {/* Editor (Right) */}
            <div className="flex-1 bg-background">
                <Editor selectedNote={activePage} />
            </div>

        </div>
    );
}
